const ReceiptHeader = ({
    icon,
    title,
    date,
}) => {
    return <ListItem>
        <ListItemIcon>
            <img src={icon} alt="" height="50" />
        </ListItemIcon>
        <Box width="10px" />
        <ListItemText primary={title} secondary={new Date(date).toLocaleString("id-ID")} />
    </ListItem>;
}

const CheckAgent = ({ id, open, setOpen, msg }) => {
    const [err, setErr] = React.useState(null);
    const data = useFetcher(open && '/biller/check/' + id, null, (err) => {
        setErr(err.message);
    });
    return <>
        <Dialog open={open} onClose={() => setOpen(false)}>
            <DialogTitle>
                <Typography align="center" variant="h6">Cek Status</Typography>
            </DialogTitle>
            <DialogContent>
                <Defer data={data || err}>
                    {() => (
                        !err ? <><Typography gutterBottom>
                            <div>Cek status menyatakan transaksi berhasil dengan data berikut:</div>
                        </Typography>
                            <Typography gutterBottom>
                                <b>{React.createElement(msg, data)}</b>
                            </Typography></> : <>
                            <Typography gutterBottom>
                                <div>Cek status menyatakan kondisi transaksi dengan pesan berikut:</div>
                            </Typography>
                            <Typography gutterBottom>
                                <b>{err}</b>
                            </Typography>
                        </>
                    )}
                </Defer>
            </DialogContent>
        </Dialog>
    </>
}
const wrap = (data, icon, fn, canCheck = null) => {
    const [open, setOpen] = React.useState(false);
    return <List>
        <ReceiptHeader icon={icon} title={data.title} date={data.timestamp} />
        {fn(data.details)}
        {canCheck && <ListItem>
            <Box flexGrow={1} />
            <Button onClick={() => setOpen(true)} color="secondary" variant="outlined">Cek Status</Button>
            <CheckAgent id={data.id} open={open} setOpen={setOpen} msg={canCheck} />
        </ListItem>}
    </List>
}

const selectiveReceipt = (data) => {
    console.log(data);
    switch ((data.product || '').split('/').slice(1, 3).join('/')) {
        case 'pln/postpaid':
            return wrap(data, b1, showPlnPostPaidReceipt, ({ IF }) => <div>Info: <b>{IF}</b></div>);
        case 'pln/nontaglis':
            return wrap(data, b1, showPlnNonTaglisReceipt);
        case 'pln/prepaid':
            return wrap(data, b1, showPlnPrePaidReceipt, ({ TK }) => <div>Token: <b>{TK.match(/(.{1,4})/g).join('-')}</b></div>);
        case 'asuransi/bpjskes':
            return wrap(data, b3, showBPJSReceipt);
        case 'air/pdam':
            return wrap(data, b4, showPdamReceipt);
        case 'pulsa/prepaid':
            return wrap(data, b2, showMobilePrepaidReceipt, ({ status }) => <div>Status: {status}</div>);
        case 'pulsa/data':
            return wrap(data, b2, showMobilePrepaidReceipt, ({ status }) => <div>Status: {status}</div>);
        case 'kabel/transvision':
            return wrap(data, b1, showTransvisionReceipt);
        case 'kabel/indovision':
            return wrap(data, b1, showIndovisionReceipt);
        case 'pulsa/postpaid':
            return wrap(data, b1, showMobilePostpaidReceipt);
        case 'finance/finansia':
            return wrap(data, b1, showFinanceReceipt);
        case 'finance/bprkredit':
            return wrap(data, b1, showFinanceReceipt);
        case 'finance/homecredit':
            return wrap(data, b1, showFinanceReceipt);
        case 'finance/otocredit':
            return wrap(data, b1, showFinanceReceipt);
        case 'transfer/dasawarsa':
            return wrap(data, b1, showTransferReceipt);
        case 'topup/bni':
            return wrap(data, b1, showTopupBniReceipt);
        default:
            return '';
    }
}


const Page = () => {
    const user = useUserInfo();
    const id = getSearchParam().get('id');
    const list = useFetcher('/debit/history/' + id);
    return (
        <>
            <Navbar />
            <Defer data={user && list}>
                {() => <div>
                    <List>
                        <ListItem>
                            <Breadcrumbs>
                                <Link color="inherit" to="/digital/history/">
                                    Arsip Pembelian
                                </Link>
                                <Typography color="textPrimary">{list.title} #{id}</Typography>
                            </Breadcrumbs>
                        </ListItem>
                        <ListItem style={{ flexDirection: 'column', alignItems: 'stretch' }}>
                            {selectiveReceipt(list)}
                        </ListItem>
                    </List>
                </div>
                }
            </Defer>
        </>
    );
}

export default Page
